@import '../../styles/variables';

.native-select {
    width: 100%;
    height: 60px;
    padding-right: 15px;
    padding-left: 15px;
    border-width: 2px;
    border-style: solid;
    border-color: $gray-light;
    color: $black;
    background-color: $white;
    font-size: $font-large;
    cursor: pointer;
}